.App {
  text-align: center;
}
.navbar-brand:hover {
  cursor: pointer;
}

.AdPanel {
  padding: 0;
  /* width: 70%; */
  background-image: url(./MainPanel.jpg);
  background-position: center;
  background-repeat: no-repeat;
  object-fit: cover;
}
.Row {
  display: flex;
  flex-wrap: wrap;
}
.col-4 > a > p {
  margin: 0;
}
.col-4 > a {
  text-decoration: none;
  color: black;
}
.col-4 > a > div {
  border: 1px solid rgba(0, 0, 0, 0.07);
  border-radius: 100%;
  width: 100%;
  overflow: hidden;
}
.col-4 > a > div > img {
  width: 100%;
  position: relative;
}
.col-4:hover a > div > img {
  /* border: 1px solid red; */
  transform: scale(1.3);
  transition: transform 0.2s;
}
.BeforePrice,
.AfterPrice {
  margin: 0px;
}
.AfterPrice {
  font-size: 20px;
  font-weight: bold;
}
.BeforePrice {
  text-decoration: line-through;
  color: red;
}
