.wow-enter {
  opacity: 0;
} //애니메이션 시작 때 적용할 css
.wow-enter-activ {
  opacity: 1;
  transition: all 200ms;
} //애니메이션 동작 때 적용할 css

.container {
  display: flex;
  flex-flow: column;
  justify-content: center;
}

@mixin alret-color {
  background: #ffe591;
  color: azure;
}
.alertBox {
  text-align: center;
}
.my-alert {
  background: #eeeeee;
  padding: 15px;
  margin: 0 auto;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  justify-content: center;
  p {
    margin: 0;
    font-weight: bold;
  }
}

.my-alert2 {
  @extend .my-alert;
  @include alret-color();
}

.AfterPrice {
  font-size: 20px;
  font-weight: bold;
}
.BeforePrice {
  text-decoration: line-through;
  color: red;
}

.btn {
  margin: 2px;
}
